.modal {
  position: fixed;
  background-color: $white;
  border-radius: 20px;
  z-index: 9999;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(1);
  @include transition(transform);

  &--welcome {
    width: 576px;
    height: 316px;
    padding: 46px 41px;
  }

  &--thanks {
    width: 463px;
    height: 245px;
    padding: 69px 43px;
    display: flex;
    justify-content: space-between;
  }

  &__close-btn {
    outline: none;
    border: none;
    background-color: transparent;
    position: absolute;
    top: 30px;
    right: 30px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    svg {
      stroke: $black;
      @include transition(stroke);
    }

    &:hover {
      cursor: $cursor;
      svg {
        stroke: $accent-color;
      }
    }
  }

  &__title {
    text-align: center;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__form {
    margin-top: 42px;
    display: flex;
    justify-content: space-between;
  }

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 18px;
  }

  &__label {
    text-align: center;
    font-family: Inter;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 18px;
  }

  &__button {
    @extend %btn;
  }

  &__images {
    display: flex;
    flex-direction: column;
    gap: 46px;

    img {
      width: 30px;
      height: 30px;
    }

    &--first {
      img:last-child {
        position: relative;
        left: 44px;
      }
    }

    &--second {
      img:last-child {
        position: relative;
        right: 44px;
      }
    }
  }
}

.overlay {
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9998;
  opacity: 1;
  @include transition(opacity);

  &.is-hidden {
    opacity: 0;
  }
}

.is-hidden {
  transform: translate(-50%, -50%) scale(0);
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}
